import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../components/layout'
import parse from 'html-react-parser'

const PortfolioPage = ({data}) => {
	return (
		<Layout>
			<section id="portfolio">
				<div className="container">
					<h2 id="portfolio-title" className="title-header opacity">Portfolio</h2>

						{data.examples.items.map(example =>
							<div className="work-description">
								<div className="assets">
									<img className="work-image" alt={example.portfolioImage.title} src={example.portfolioImage.file.url}/>
									<div className="work-links">
										{example.webLinks ? example.webLinks.map(({displayText, url, icon}) => 
											<a href={url} target="_blank" rel="noopener noreferrer"><div className="button"><i className={`fa fa-${icon}`}></i> {displayText}</div></a>
										) : null}
									</div>
									<div className="gallery">
									
									</div>
								</div>

								<div className="content">
									<h3 className="title-header">{example.name}</h3>
									<h5 className="sub-header">{example.role}</h5>
									{parse(example.description.description)}
									<h3>Tech Used</h3>
									<ul>
										{example.techItems.map(item => <li>{item}</li>)}	
									</ul>
								</div>
							</div>
						)}

				</div>
			</section>
		</Layout>
	)
}

export default PortfolioPage

export const query = graphql`
  	query PortfolioPageQuery {
	    examples: contentfulOrder(name: {eq: "Portfolio Items"}) {
	    	items {
	        	id
	        	name
	        	role
	        	description {
	          		description
	        	}
	        	techItems
	        	webLinks {
	          		displayText
	          		url
	          		icon
	        	}
	        	portfolioImage {
	          		file {
	            		url
	            		fileName
	          		}
	        	}
	        	screenShots {
	          		id
	          		file {
	            		url
	            		fileName
	            
	          		}
	        	}
	      	}
	    }
	}
 `
